import React from 'react'

const FooterMenu = () => {
	return (
		<div>
			<footer>
			    <div className="footer_logo_main">
			        <div className="footer_line1"></div>
			        <div className="footer_line2"></div>
			    </div>
			    <div className="footer_line1"></div>
			    <div className="footer_line2"></div>
			    <div className="footer_logo"></div>
			    <ul>
			        <li>&#169; {(new Date().getFullYear())} HEPTA</li>
			        <li><a href="/privacy-policy">privacy</a></li>
			        <li><a href="/terms-and-conditions">terms</a></li>
			        <li><a href="/talk-to-us">contact</a></li>
			    </ul>
			    <ul className="social">
			    {/*<!-- <li><img src="images/twit.png"></li> -->*/}
			        <li>
			        	<a href="https://www.facebook.com/hepta.me" target="_blank" rel="nofollow">
			        		<img src={require("./../images/fb.png")}/>
			        	</a>
			        </li>
			        <li>
			        	<a href="https://www.instagram.com/allyourweb/" target="_blank" rel="nofollow">
			        	<img src={require("./../images/insta.png")}/>
			        	</a>
			        </li>
			    </ul>
			    <div className="pattern">
			        <div className="pattern_in color1"></div>
			        <div className="pattern_in color2"></div>
			        <div className="pattern_in color3"></div>
			    </div>
			</footer>
		</div>
	)
}

export default FooterMenu