import React from 'react'
import $ from 'jquery';
import logo from './../images/logo.png';
import { NavLink } from "react-router-dom";

// const MainMenu = () => {
class MainMenu extends React.Component{
  	componentDidMount() {
    	this.updateDimensions();
  	} 
  	updateDimensions = () => {
      	document.getElementsByClassName("menu-gap")[0].style.height = document.getElementsByClassName("main-menu")[0].clientHeight  + "px";
  	}
  	menu_open(){
  		$('.form_click').toggleClass("active_menu");
        $("header").toggleClass("active_header");
        $(".contact_form").fadeToggle();
        $("header nav").fadeToggle();
        /*document.getElementsByClassName("form_click")[0].classList.toggle("active_menu");
        document.getElementsByTagName("header")[0].classList.toggle("active_header");*/
  	}
	render(){
		return (
			<div>
				<meta content="Faraz Naqvi" itemProp="location" data-name="author"/>
				<link rel="publisher" href="https://plus.google.com/u/0/103574226385523408983/posts" property='og:site_name'/>
				<div className="contact_form">
				    <table>
					    <tbody>
					        <tr>
								<td>
									<div className="other_nav">
										<ul>
											<li><NavLink to="/get-to-know-us" onClick={this.menu_open}>About</NavLink></li>
											<li><NavLink to="/what-keeps-us-busy" onClick={this.menu_open}>Services</NavLink></li>
											<li><NavLink to="/what-makes-us-happy" onClick={this.menu_open}>Work</NavLink></li>
											<li><NavLink to="/talk-to-us" onClick={this.menu_open}>Contact</NavLink></li>
					                    </ul>
									</div>
									<p>Like our work? If you would like to discuss your project or visit us to have some <span>chai</span>, drop in a line below and we will reach out to you shortly.</p>
									{/*<!-- <NavLink to="https://docs.google.com/a/hepta.me/forms/d/17vMDsQ9ErIal0Dy5qhz9b70VTLWIDTuR8Cjtt3XoaUk/viewform" target="_blank"> 
										<div className="contact_form_btn">Contact Form</div>
									</NavLink> -->*/}
					            </td>
					        </tr>
				        </tbody>
				    </table>
				</div>
				<header className="main-menu" ref="main_menuContainer">
					<div className="logo">
				        <NavLink to="/"><img src={logo} alt="hepta logo" /></NavLink>
				    </div>	
				    <nav>
				        <ul>
							<li><NavLink to="/get-to-know-us">about</NavLink></li>
							<li><NavLink to="/what-keeps-us-busy">services</NavLink></li>
							<li><NavLink to="/what-makes-us-happy">work</NavLink></li>
							<li><NavLink to="/talk-to-us">contact</NavLink></li>
						</ul>
				    </nav>
					<div className="form_click" onClick={this.menu_open}>
				        <span className="line1 color1"></span>
						<span className="line2 color2"></span>
						<span className="line3 color3"></span>
				    </div>
				</header>
				<div className="menu-gap"></div>
				{/*<canvas id="canvas"></canvas>*/}
			</div>
		);
	}
}

export default MainMenu